import React from 'react';
import styles from './Header.module.css';
import classNames from 'classnames/bind';
import useNav from '../../../hooks/useNav';
import { BorderButton, BottomButton } from '../../common/Button/Buttons';
const cx = classNames.bind(styles);

export default function Header() {
  const { toAbout, toPortfolio, toProject, toSignin, toSignup } = useNav();
  return (
    <header className={cx('Header')}>
      <h1 onClick={toAbout}>SPEC</h1>
      <nav className={cx('navigate')}>
        <BottomButton onClick={toProject}></BottomButton>
        <BottomButton onClick={toPortfolio}></BottomButton>
      </nav>
      <div className={cx('actions')}>
        <BorderButton onClick={toSignin}></BorderButton>
        <BorderButton onClick={toSignup}></BorderButton>
      </div>
    </header>
  );
}
